import { WalletDoctorModel } from './../../../../home/utils/models/walletDoctorModel';
import { Injectable } from '@angular/core';
import { catchError, map } from 'rxjs/operators'
import { environment } from '@app/env';
import { ServiceBag } from '@app/shared'


@Injectable()
export class WalletService {


  constructor(private http: ServiceBag) { }

  private doctorApi = environment.doctorApi;

  getWallet(params: any) {
    return this.http.get<WalletDoctorModel>(this.doctorApi + '/GetWallet', params).pipe(map(res => {
      return res;
    }));
  }

}
