import { Component, OnInit } from '@angular/core';
import { WalletService, WalletParams } from '../utils';
import { WalletDoctorModel } from '@app/shared';

@Component({
  selector: 'app-wallet',
  templateUrl: './wallet.component.html'
})
export class WalletComponent implements OnInit {

  constructor(private service: WalletService) { }

  model: WalletDoctorModel = new WalletDoctorModel();
  walletParams: WalletParams = new WalletParams();
  ngOnInit(): void {
    this.getDoctorWallet();
  }


  getDoctorWallet() {
    this.service.getWallet(this.walletParams).subscribe(res => {
      this.model = res.result;
    });
  }
}
